import React, { useEffect, useCallback } from 'react';
import { withRouter } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { changeField, initialize, loginPost } from '../modules/login';
import LoginForm from '../components/auth/LoginForm';
import { setCookie, getCookie } from '../lib/cookie';

const LoginContainer = ({ history }) => {
    const dispatch = useDispatch();
    const { id, password, login, error } = useSelector(({ login }) => ({
        id: login.id,
        password: login.password,
        login: login.login,
        error: login.error,
    }));

    //   
    const onChange = useCallback(payload => dispatch(changeField(payload)), [dispatch]);

    useEffect(() => {
        const token = getCookie('user');
        if (token) {
            history.push('/notification');
        }
        if (!token) {
            history.push('/');
        }
    }, [history]);

    //        
    useEffect(() => {
        dispatch(initialize());
    }, [dispatch]);

    //     
    useEffect(() => {
        return () => {
            dispatch(initialize());
        }
    }, [dispatch]);

    //  
    const onSubmit = e => {
        dispatch(loginPost({ id, password }));
    };

    //    
    useEffect(() => {
        if (login) {
            if (login) {
                alert(" ");
                setCookie('user', login.token, 1);
                localStorage.setItem("users", 'users');
                history.push('/notification');    //  
            }
        }
        if (error) {
            if (error) {
                alert(" ");   //   
                history.push('/');
            }
        }
    }, [history, login, error, dispatch]);

    return <LoginForm
        onChangeField={onChange}
        onSubmit={onSubmit}
        id={id}
        password={password}
    />;
};


export default withRouter(LoginContainer);