import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { logout } from '../../modules/login';
import { Link } from 'react-router-dom';
import { getCookie, deleteCookie } from '../../lib/cookie';

const NotificationPageSpanBox = styled.div`
width: 80%;
margin-left: -10px;
height: 100%;
margin-bottom: -0px;
background: #00000;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;

span {
    font-size: 50px;
    color: white;
    margin-top: 25px;
}

font-family: 'Do Hyeon', sans-serif;
color: white;  
`;

const StyledButton = styled.button`
position: absolute;
margin-top: -300px;
border: 2px solid #FFFFFF;
box-sizing: border-box;
width: 76px;
height: 52px;
left: 1330px;
top: 354px;
background: #000000;
border-radius: 3px;
text-align: center;
color: white;
font-size: 15px;
cursor: pointer;
z-index: 0;
`;

const NotificationPage = ({ data, history, location }) => {

    const [login, setLogin] = useState(null);
    const dispatch = useDispatch();

    const onLogout = () => {
        dispatch(logout());
        deleteCookie('user');
        alert(" ");
        window.location.reload();
    };

    useEffect(() => {
        const token = getCookie("user");
        if (token === null) {
            setLogin(false);
        }
        if (token) {
            setLogin(true);
        }
    }, []);

    return (
        <NotificationPageSpanBox>
            <span>INFO CTF   !</span>
            <span style={{ fontSize: '40px' }}>-     -</span>
            <span style={{ fontSize: '24px' }}>*       </span>
            <span style={{ fontSize: '24px' }}>*  MyPage  !</span>
            <span style={{ fontSize: '24px' }}>*     .. .</span>
            <span style={{ fontSize: '24px' }}>* ex) ,   ...</span>
            <span style={{ fontSize: '14px', color: 'black' }}> : , </span>
        </NotificationPageSpanBox>
    );
};

//  {login ? <StyledButton onClick={onLogout}></StyledButton> : <Link to="login"><StyledButton style={{ textDecoration: 'none', zIndex: 0 }}></StyledButton></Link>}

export default withRouter(NotificationPage);

