import React, { useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { setCookie } from '../../lib/cookie';
import { withRouter, Redirect } from 'react-router-dom';

const AdminLoginArea = styled.div`
    padding: 2rem;
    width: 100%;
    background: #000000;
    border-radius: 2px;
    height: 700px;
    margin-bottom: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .ButtonArea {
        width: 604.5px;
    }

    .findpassword {
        font-family: Noto Sans KR;
        font-style: normal;
        font-weight: 300;
        font-size: 30px;
        line-height: 51px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #5A5A5A;
        margin-right: 350px;
    }

    /*       */
    @media (max-width: 1024px) {
        width: 100%;
    }
    @media (max-width: 768px) {
        width: 100%;
    }
    
    h1 {
        color: white;
    }
    font-family: 'Do Hyeon', sans-serif;
    color: white;
`;

const StyledInput = styled.input`
    font-size: 2rem;
    border: 2px solid #FFFFFF;
    box-sizing: border-box;
    width: 600px;
    height: 52px;
    left: 642px;
    top: 420px;
    background: #000000;
    border-radius: 3px;
    text-align: center;
    color: white;
`;

const StyledButton = styled.button`
font-size: 2rem;
border: 2px solid #FFFFFF;
box-sizing: border-box;
width: 299.5px;
height: 52px;
left: 642px;
top: 420px;
background: #000000;
border-radius: 3px;
text-align: center;
color: white;
`;

const AdminLoginForm = ({ history }) => {

    const [login, setLogin] = useState({ id: '', password: '' });

    const onChange = e => {
        setLogin({ ...login, [e.target.name]: e.target.value });
    };

    const onSubmit = e => {
        e.preventDefault();
        axios.post(("http://211.35.225.252:4000/api/admin/login"), {
            id: login.id,
            password: login.password,
        }).then(result => {
            if (result.status === 201) {   //  
                alert(" !");
                if (!result.data.token) {
                    alert("");
                    return;
                }
                setCookie("user", result.data.token);
                localStorage.setItem("admin", 'admin');
                history.push('/notification');
            }
            else if (result.status === 403) {
                alert(" ");
                return <Redirect to="/adminlogin" />
            }
        })
    }

    return (
        <>
            <AdminLoginArea>
                <h1> </h1>
                <StyledInput name="id" onChange={onChange} placeholder="id" />
                <br />
                <StyledInput type="password" name="password" onChange={onChange} placeholder="password" />
                <br />
                <div className="ButtonArea">
                    <StyledButton><Link to="/adminregister" style={{ textDecoration: 'none', color: 'white' }}>sign in</Link></StyledButton>
                            &nbsp;
                        <StyledButton onClick={onSubmit}>login</StyledButton>
                </div>
                <span className="findpassword"><Link to="/findpassword" style={{ textDecoration: 'none', color: 'gray' }}>find password</Link></span>
            </AdminLoginArea>
        </>
    );

};

export default withRouter(AdminLoginForm);
