import React from 'react';
import styled from 'styled-components';

const RankBox = styled.div`
width: 80%;
height: 100%;
background: #000000;


table {
    width: 100%;
    font-size: 24px;
    text-align: center;
    margin-top: 100px;
}

font-family: 'Do Hyeon', sans-serif;
color: white;
`;




const RankItem = ({ ranks, mydata, loading }) => {

    if (loading) {
        return (
            <RankBox>
            <h1 style={{ fontFamily: 'Do Hyeon', color: 'white', marginLeft: '500px',marginTop: '250px'}}>   .</h1>
            </RankBox>
        );
}

    return (
        <>
            <RankBox>
                <table>
                    <colgroup>
                        <col width="18%" />
                        <col width="20%" />
                        <col width="20%" />
                    </colgroup>
                    <RankRow ranks={ranks} />
                </table>
            </RankBox>

        </>
    );
};

//      
const RankRow = ({ ranks }) => {

    // rank    (           )
    if (ranks === null) {
        return (
            <RankBox>
            <h1 style={{ color: 'white', marginLeft: '220px', marginTop: '200px' }}>  . <br />   .</h1>
            </RankBox>
        );
    }

    if (!ranks) {
        return null;
    }

    return (
        <>
            <thead>
                <tr>
                    <th>-rank-</th>
                    <th>-name-</th>
                    <th>-score-</th>
                </tr>
            </thead>
            {ranks.map(rank => {
                return (
                    <>
                        <RankList key={rank.rank} ranks={rank} />
                    </>
                )
            })}
        </>
    );
}

//      
const RankList = ({ ranks }) => {
    const { name, score, rank } = ranks;
    return (
        <>

            <tbody>
                <tr>
                    <td>{rank}</td>
                    <td>{name}</td>
                    <td>{score}</td>
                </tr>
            </tbody>
        </>
    );
}

export default RankItem;
