import React from 'react';
import styled from 'styled-components';

const LoginAfterArea = styled.div`
    padding: 2rem;
    width: 560px;
    background: black;
    border-radius: 2px;
    height: 500px;
    margin-top: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 25px;

    .ButtonArea {
        width: 604.5px;
    }

    .findpassword {
        font-family: Noto Sans KR;
        font-style: normal;
        font-weight: 300;
        font-size: 35px;
        line-height: 51px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #5A5A5A;
        margin-right: 346px;
    }

    /*       */
    @media (max-width: 1024px) {
        width: 100%;
    }
    @media (max-width: 768px) {
        width: 100%;
    }

    font-family: 'Do Hyeon', sans-serif;
    color: white;  
`;

const StyledInput = styled.input`
    font-size: 2rem;
    border: 2px solid #FFFFFF;
    box-sizing: border-box;
    width: 600px;
    height: 52px;
    left: 642px;
    top: 420px;
    background: #000000;
    border-radius: 3px;
    text-align: center;
    color: white;
`;

const StyledButton = styled.button`
font-size: 2rem;
border: 2px solid #FFFFFF;
box-sizing: border-box;
width: 299.5px;
height: 52px;
left: 642px;
top: 420px;
background: #000000;
border-radius: 3px;
text-align: center;
color: white;
`;


const LoginAfterForm = ({ loading, mypage, onLogout, onChangeField, onSubmit, modifiedid, modifiedpassword, modifiednickname, modifiedemail, modifiedteam }) => {

    if (loading) {
        return <LoginAfterArea><h1 style={{ textDecoration: 'none', color: 'white' }}>  .</h1></LoginAfterArea>
    }

    if (mypage === null) {
        return <LoginAfterArea><h1 style={{ textDecoration: 'none', color: 'white' }}>  .</h1></LoginAfterArea>
    }

    if (!mypage) {
        console.log("");
        return null;
    }

    const onChangeInput = e => {
        const { name } = e.target;
        onChangeField({ key: name, value: e.target.value });
    };

    const { id, nickname, email, score, team } = mypage;

    return (
        <>
            {/*   , mypage     */}
            {!loading && mypage && (
                <LoginAfterArea>
                    ID<StyledInput type="text" name="id" placeholder={id} defaultValue={modifiedid} onChange={onChangeInput} />
                    E-Mail<StyledInput type="text" name="email" placeholder={email} value={modifiedemail} onChange={onChangeInput} />
            
                    Nickname<StyledInput type="text" name="nickname" placeholder={nickname} value={modifiednickname} onChange={onChangeInput} />
               
                    Team<StyledInput type="text" name="team" placeholder={team} value={modifiedteam} onChange={onChangeInput} />
                 
                    <StyledInput name="score" defaultValue={score} readOnly />
                    <br />
                    <div className="ButtonArea">
                        <StyledButton onClick={onLogout} style={{ textDecoration: 'none', color: 'white' }}>log out</StyledButton>
                            &nbsp;
                        <StyledButton onClick={onSubmit} style={{ textDecoration: 'none', color: 'white' }}>submit</StyledButton>
                    </div>
                </LoginAfterArea>
            )}
        </>
    );

};

export default LoginAfterForm;
